.profile {
  margin: 36px 0 50px 0;
  display: grid;
  grid-template-areas:
    "picture name btn-add"
    " picture about btn-add";
  grid-template-columns: 150px auto 150px;
}

.profile__picture {
  grid-area: picture;
  border-radius: 50%;
  width: 120px;
  height: 120px;
}
.profile__name-edit {
  display: flex;
  align-self: end;
}
.profile__name {
  grid-area: name;
  align-self: center;
  font-size: 42px;
  font-weight: 500;
  line-height: 48px;
  margin: 0 0 16px 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 373px;
}
.profile__button-edit {
  margin: 0 0 16px 18px;
  padding: 0;
  grid-area: btn-edit;
  align-self: center;
  border: 1px solid #ffffff;
  background: url("../images/edit__button.svg");
  background-repeat: no-repeat;
  background-position: center;
  width: 24px;
  height: 24px;
  background-color: black;
}
.profile__button-edit:hover {
  cursor: pointer;
  opacity: 60%;
}
.profile__subtitle {
  grid-area: about;
  margin: 0;
  font-size: 18px;
  font-weight: 400;
  line-height: 22px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 415px;
}

.profile__button-add {
  grid-area: btn-add;
  justify-self: end;
  border-radius: 2px;
  border: 2px solid #ffffff;
  align-self: center;
  background: url("../images/add_button.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-color: black;
  width: 150px;
  height: 50px;
  font-size: 22px;
}
.profile__container{
  width: 120px;
  height: 120px;
  position: relative;
}
.profile__edit-avatar{
  position: absolute;
  top: 0;
  left: 0;
  border: 0;
  padding: 0;
  outline: none;
  width: 100%;
  height: 100%;
  opacity: 0;
  border-radius: 50%;

  background: rgba(0,0,0, 0.8) url("../images/edit__button.svg") no-repeat center;
  background-size: 26px;
  transition: all 0.3s;
}
.profile__edit-avatar:hover{
  opacity: 1;
  cursor: pointer; 
}


.profile__button-add:hover {
  opacity: 60%;
  cursor: pointer;
}

@media screen and (max-width: 750px) {
  .profile {
    grid-template-columns: auto 220px auto;
  }
  .profile__button-edit {
    margin: 0 0 0 10px;
    align-self: center;
  }
  .profile__name {
    font-size: 4.667vw;
  }
}
@media screen and (max-width: 553px) {
  .profile {
    margin: 38px 0 0 0;
    text-align: center;
    display: block;
  }
  .profile__picture {
    margin: 0 0 26.25px 0;
  }
  .profile__name-edit {
    justify-content: center;
  }
  .profile__name {
    margin: 0 0 14px 0;
  }
  .profile__subtitle {
    margin: 0 auto 25px auto;
    font-size: 14px;
    line-height: 16.94px;
  }
  .profile__button-add {
    width: 282px;
    margin: 0 0 36px 0;
    padding: 0;
  }
  .profile__button-edit {
    border: 1px solid #ffffff;
    margin: 0 0 14px 10px;
    align-self: center;
    width: 18px;
    height: 18px;
  }
}
@media screen and (max-width: 385px) {
  .profile__name {
    font-size: 22px;
    line-height: 27px;
  }
}
